<template>
  <BaseScroll
    :data="artistData"
    :listenScroll="listenScroll"
    :probeType="probeType"
    @scroll="scroll"
    class="scroll-wrapper"
    ref="list"
  >
    <ul>
      <li
        class="list-group"
        v-for="(group, index) of artistData"
        :key="index"
        ref="group"
        >
        <h2 class="list-group-title">{{ group.title }}</h2>
        <ul>
          <li
            class="list-group-item"
            v-for="(item, childIndex) of group.items"
            :key="childIndex"
            @click="selectItem(item)"
          >
            <!--  -->
            <img v-lazy="item.avatar" width="50" height="50" alt="artist-avatar">
            <span class="artist-name">{{ item.name }}</span>
          </li>
        </ul>
      </li>
    </ul>

    <div class="list-shortcut" v-show="artistData.length">
      <ul>
        <li
          v-for="(item, index) of shortcutList"
          :key="index"
          :data-index="index"
          :class="['list-shortcut-item', currentIndex === index ? 'active' : '']"
          @touchstart="onShortcutToTouchStart"
          @touchmove.stop.prevent="onShortcutToTouchMove"
        >
          <span class="list-shortcut-content">{{ item }}</span>
        </li>
      </ul>
    </div>
    <div
      class="fixed-title"
      v-show="scrollY < 0"
      :style="translateY"
    >
      <h1 class="fixed-title-content">{{ fixedTitle }}</h1>
    </div>

    <BaseLoading v-if="artistData.length === 0"/>
  </BaseScroll>
</template>

<script>
/**
 *  this.listHeight
 *  this.listHeight this.listHeight  artistData 
 */
import BaseScroll from 'base/base-scroll'
import { getData } from 'common/js/control-dom'
import BaseLoading from 'base/base-loading/base-loading'

const ANCHOR_HEIGHT = 18 //  li 
const TITLE_HEIGHT = 30 // 

export default {
  props: {
    artistData: {
      type: Array,
      default () {
        return []
      }
    }
  },

  data () {
    return {
      scrollY: -1, //  scrollY  window.scrollY
      currentIndex: 0, //  title  index
      difference: 0 // scrollY  i + 1  y 
    }
  },

  computed: {
    shortcutList () {
      return this.artistData.map(group => {
        return group.title.substr(0, 1) // 
      })
    },

    fixedTitle () {
      return this.artistData[this.currentIndex] ? this.artistData[this.currentIndex].title : []
    },

    translateY () {
      if (this.difference && this.difference < TITLE_HEIGHT) {
        return {
          transform: `translateY(${-(TITLE_HEIGHT - this.difference)}px)`
        }
      } else {
        return {}
      }
    }
  },

  methods: {
    //  index  better-scroll 
    onShortcutToTouchStart (evt) {
      let anchorIndex = getData(evt.target, 'index') //  data-index 
      this.touch.y1 = evt.touches[0].pageY // pageY  HTML  Y 
      this.touch.anchorIndex = anchorIndex
      this._scrollTo(anchorIndex)
    },
    //  touchmove  list 
    onShortcutToTouchMove (evt) {
      this.touch.y2 = evt.touches[0].pageY
      let jumpNumber = (this.touch.y2 - this.touch.y1) / ANCHOR_HEIGHT | 0 //  0
      let anchorIndex = parseInt(this.touch.anchorIndex) + jumpNumber // anchorIndex 
      this._scrollTo(anchorIndex)
    },

    refresh () {
      this.$refs.list.refresh()
    },

    scroll (pos) {
      this.scrollY = pos.y
    },

    _scrollTo (index) {
      if (!index && index !== 0) { //  index  null
        return
      }
      if (index < 0) {
        index = 0
      } else if (index > this.listHeight.length - 2) {
        index = this.listHeight.length - 2
      }
      this.scrollY = -this.listHeight[index] //  active 
      this.$refs.list.scrollToElement(this.$refs.group[index], 0) // , 0
    },

    _calculateHeight () {
      this.listHeight = []
      const list = this.$refs.group
      let height = 0
      this.listHeight.push(height)

      for (let i = 0; i < list.length; i++) {
        // clientHeight = height + padding - 
        height += list[i].clientHeight
        this.listHeight.push(height)
      }
    },

    selectItem (item) { // 
      this.$emit('select', item)
    }
  },

  watch: {
    artistData () {
      setTimeout(() => {
        this._calculateHeight()
      }, 20)
    },

    scrollY (newY) {
      const listHeight = this.listHeight //  this.listHeight this.listHeight

      // newY > 0
      if (newY > 0) {
        this.currentIndex = 0
        return
      }
      // 
      for (let i = 0; i < listHeight.length - 1; i++) {
        let heightStart = listHeight[i]
        let heightEnd = listHeight[i + 1]
        if (-newY >= heightStart && -newY < heightEnd) {
          this.currentIndex = i
          this.difference = this.listHeight[i + 1] - (-newY)
          return
        }
      }
      //  -newY > 
      this.currentIndex = listHeight.length - 2
    }
  },

  created () {
    // vue  data  props  getter  setter ( DOM)

    // this.touch ( DOM) created 
    this.touch = {} // 

    this.listenScroll = true //  base-scroll

    this.listHeight = [] // 

    this.probeType = 3 // better-scroll 0 touch 
  },

  components: {
    BaseScroll,
    BaseLoading
  }
}
</script>

<style lang="scss" scoped>
@import '~scss/variables';

%title {
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
  font-size: $font-size-small;
  color: $color-text-l;
  background-color: $color-highlight-background;
}

.scroll-wrapper {
  position: relative;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 100%; // 
  font-size: 0;
  .list-group {
    padding-bottom: 30px;
    .list-group-title {
      @extend %title
    }
    .list-group-item {
      display: flex;
      align-items: center;
      padding: 20px 0 0 30px;
      img {
        border-radius: 50%;
      }
      .artist-name {
        margin-left: 20px;
        color: $color-text-l;
        font-size: $font-size-medium;
      }
    }
  }
  .list-shortcut {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    z-index: 10;
    width: 20px;
    padding: 20px 0;
    text-align: center;
    border-radius: 10px;
    background-color: $color-background-d;
    .list-shortcut-item {
      padding: 3px;
      line-height: 1;
      color: $color-text-l;
      &.active {
        color: $color-theme;
      }
      .list-shortcut-content {
        font-size: $font-size-small;
      }
    }
  }
  .fixed-title {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    .fixed-title-content {
      @extend %title
    }
  }
  .loading {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
}
</style>



