/**
 * mixin 
 */

import { mapGetters, mapMutations, mapActions } from 'vuex'
import { playMode } from 'common/js/config'
import { shuffle } from 'common/js/util'

export const playlistMixin = {
  computed: {
    ...mapGetters([
      'playlist' //  uex  state 
    ])
  },

  mounted () {
    this.handlePlaylist(this.playlist)
  },

  activated () {
    this.handlePlaylist(this.playlist)
  },

  watch: {
    playlist (newList) {
      this.handlePlaylist(newList)
    }
  },

  methods: {
    //  playlist.length === 0  bottom: 60px
    handlePlaylist () {
      throw new Error('Component must implement handlePlaylist method')
    }

    // 

    // handlePlaylist (playlist) {
    //   const bottom = playlist.length > 0 ? '60px' : ''

    //   this.$refs.scrollWrapper.style.bottom = bottom
    //   // ... refresh component scroll
    // }
  }
}

export const playerMixin = {
  methods: {
    toggleIsFavorite (song) {
      this.isFavoriteSong(song) ? this.deleteMyFavoriteSong(song) : this.saveMyFavoriteSong(song)
    },

    getFavoriteIcon (song) {
      return this.isFavoriteSong(song) ? 'icon-like' : 'icon-not-like'
    },

    isFavoriteSong (song) {
      return this.myFavoriteSongs.findIndex(item => item.id === song.id) > -1
    },

    togglePlayMode () {
      const mode = (this.mode + 1) % 3
      this.setPlayMode(mode)

      let list = null
      if (mode === playMode.random) {
        list = shuffle(this.sequenceList) // 
      } else {
        list = this.sequenceList
      }
      this.resetCurrentIndex(list)
      this.setPlaylist(list)
      /**
       * playlist  currentSong  watch 
       *  console currentIndex  watcher  currentSong 
       * 
       */
    },
    //  playlist 
    resetCurrentIndex (list) {
      let index = list.findIndex(item => {
        return item.id === this.currentSong.id
      }) //  list  index

      this.setCurrentIndex(index)
    },

    ...mapMutations({
      setPlayingState: 'SET_PLAYING_STATE',
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setPlayMode: 'SET_PLAY_MODE',
      setPlaylist: 'SET_PLAYLIST'
    }),

    ...mapActions([
      'saveMyFavoriteSong',
      'deleteMyFavoriteSong'
    ])
  },

  computed: {
    iconMode () {
      return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.loop ? 'icon-loop' : 'icon-random'
    },

    ...mapGetters([
      'playlist',
      'mode',
      'sequenceList',
      'currentSong',
      'myFavoriteSongs'
    ])
  }
}

export const searchMixin = {
  data () {
    return {
      queryKey: '',
      refreshScrollDelay: 100 //  transition-group ( search-list )
    }
  },

  methods: {
    selectQueryRecord (item) {
      /**
       * :
       * 1.  props  watch
       * 2. 
       * 
       */

      this.$refs.searchBox.setQuery(item)
    },

    getQueryKey (query) {
      this.queryKey = query
    },

    blurInputBox () {
      this.$refs.searchBox.blurInputBar()
    },

    saveSearchItem () {
      this.saveSearchHistory(this.queryKey)
    },

    ...mapActions([
      'saveSearchHistory',
      'deleteSearchHistory' // 
    ])
  },

  computed: {
    ...mapGetters([
      'searchHistory'
    ])
  }
}



