import { RANKING_LIST_URL, FULL_RANKING_LIST_URL, options, commonParams } from './config'
import jsonp from 'common/js/jsonp'

// start

//  toplist jsonp 

/**
 *  jsonp  ajax  jsonp 
 *  url 
 */

export function getRankingList () {
  const data = {
    page: 'index',
    format: 'html',
    tpl: 'macv4',
    v8debug: 1
  }

  return jsonp(RANKING_LIST_URL, data, {
    ...options,
    prefix: '',
    name: 'jsonCallback'
  })
}

