//  urljsonp 
export function getFullRankingList (id) {
  const data = {
    ...commonParams,
    tpl: 3,
    page: 'detail',
    // data: 
    topid: id,
    type: 'top',
    song_begin: 0,
    song_num: 30,
    loginUin: 0,
    hostUin: 0,
    platform: 'yqq',
    needNewCode: 0
  }

  return jsonp(FULL_RANKING_LIST_URL, data, {
    ...options,
    prefix: '',
    name: 'MusicJsonCallbacktoplist'
  })
}



